/* eslint-disable prefer-template */
/*
 * Tabs/ContactTab.js
 *
 * The ContactTab component is displayed on the /contact route
 */

import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { Grid, Card, CardMedia, CardContent, CardActions, Chip, Link, Typography }
  from '@material-ui/core';
import { GoogleMapsAPIKey } from '../secret/GoogleMapsAPI';

const styles = {
  root: {
    padding: '75px 0 10px',
    minHeight: '100vh',
    width: '98%',
    margin: '0 auto',
  },
  card: {
    padding: '8px 1%',
  },
  body: {
    padding: '8px 0',
  },
  img: {
    height: '200px',
    width: '200px',
    margin: '0 auto',
  },
  content: {
    padding: '16px 4px',
  },
  chipBox: {
    display: 'flex',
    justifyContent: 'space-around',
    flexWrap: 'wrap',
  },
  chip: {
    margin: '1%',
    fontWeight: '500',
  },
  link: {
    margin: '0 auto',
  },
};

const MAP_LINK = 'https://maps.googleapis.com/maps/api/staticmap?size=300x300&zoom=6&'
  + 'markers=color:0x303030%7C26.1581,-80.3254&center=27.7,-81.5639&key='
  + GoogleMapsAPIKey
  + '&signature=T3IEGHMaW-oRUvO0QnYbCwEf22o=';

const ContactTab = ({ classes }) => (
  <Grid
    container
    spacing={1}
    justify="center"
    className={classes.root}
  >
    <Grid item sm={12} lg={11}>
      <Card className={classes.card}>
        <CardMedia image={MAP_LINK} className={classes.img} />
        <CardContent>
          <Typography variant="body1" className={classes.body}>
            I currently reside in southern Broward County Florida.
            I would be interested in hearing about opportunities in South or Central Florida,
            including these markets:
          </Typography>
          <div className={classes.chipBox}>
            {['Fort Lauderdale', 'Miami', 'West Palm Beach', 'Fort Myers', 'Tampa', 'Orlando'].map(
              city => (
                <Chip label={city} key={city} color="secondary" className={classes.chip} />
              ),
            )}
          </div>
        </CardContent>
        <CardActions>
          <Link
            underline="none"
            href="https://www.linkedin.com/in/danburkhardt"
            target="_blank"
            rel="noopener noreferrer"
            className={classes.link}
            variant="button"
            color="secondary"
          >
            Contact me on LinkedIn
          </Link>
        </CardActions>
      </Card>
    </Grid>
  </Grid>
);

ContactTab.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ContactTab);
